<template>
	<div>
		<header-top :nav ="nav"></header-top>
		<!-- <index-korea v-if="currentCountry == '韩国'"></index-korea> -->
		<!-- <index-japanyy></index-japanyy> -->
		<div id="jpyy">
	        <div class="bannerPart">
	            <img src="../../../static/images/school/schoolBanner.jpg">
	        </div>
	        <ul class="selectcity container">
	            <li class="city_item">
                    <router-link to="/school">语言学校</router-link>
                </li>
                <li class="city_item">
                    <router-link to="/school/jpspecial">专门学校</router-link>
                </li>
                <li class="city_item">
                    <router-link to="/school/jpco">日本大学</router-link>
                </li>
                <li class="city_item active">
                    <router-link to="/school/jpsenior">日本高中</router-link>
                </li>
	        </ul>
	        <!-- <div class="container choosepart">
	            <div class="lineDv clearfix">
	                <span class="s_title">热门排行：</span>
	                <ul class="listul">
	                    <li class="item" v-for="n in 5">规模较大的语言学校</li>
	                </ul>
	            </div>
	            <div class="lineDv choose clearfix">
	                <span class="s_title">学校名称：</span>
	                <div class="inputDv">
	                    <input type="text" placeholder="请输入学校名称" v-model="request.name" @keyup.13="change('schoolname',request.name)">
	                    <i class="search" @click="change('schoolname',request.name)"><img src="../../../static/images/school/search_icon.png"></i>
	                </div>
	            </div>
	            <div class="lineDv clearfix">
	                <span class="s_title">选择地区：</span>
	                <select name="" id="area" class="select2" width="290">
	                    <option value="">全部</option>
	                    <option :value="n" v-for="n in area" :key = "n">{{n}}</option>
	                </select>
	                <span class="s_title">选择城市：</span>
	                <select name="" id="city" class="select2" width="290">
	                    <option value="">全部</option>
	                    <option :value="n.id" v-for="n in city">{{n.name}}</option>
	                </select>
	            </div>
	            <div class="lineDv choose clearfix">
	                <span class="s_title">学费：</span>
	                <ul class="listul">
	                    <li class="item active" @click="change('cost_fee','')">不限</li>
	                    <li class="item" v-for="n in tuition" @click="change('cost_fee',n.id)">{{n.name}}</li>
	                </ul>
	            </div>
	        </div> -->
	        <div class="resultPart container">
	            <h3 class="search_title">共找到<font>{{count}}</font>所学校</h3>
	            <ul class="schoolList">
	                <li class="item_school clearfix" v-for="n in lists" :key="n.id">
	                    <div class="imgDv left">
	                        <router-link :to="{ name: 'Schooldetails', params: { id: n.id }}">
	                        <img :src="'http://manage.xiaoying.net/Uploads/resources/school/'+n.defaultimage" alt="">
	                        </router-link>
	                    </div>
	                    <div class="info left">
	                        <router-link :to="{ name: 'Schooldetails', params: { id: n.id }}">
	                        <!-- <a :href="'/school/jpyy/detail?id='+n.id"> -->
	                            <h3 class="name"><span>{{n.name}}</span><span>{{n.englishname}}</span></h3>
	                            <ul class="slistDv">
	                                <li class="slineDv">学校类型：{{n.category}}</li>
	                                <li class="slineDv">每年学费：{{n.schoolfee | filterfee}}万(日元)</li>
	                                <li class="slineDv">学校性质：{{n.attribute}}</li>
	                                <li class="slineDv">电话：{{n.tel?n.tel:'暂无'}}</li>
	                                <li class="slineDv">所在地区：{{n.area}}</li>
	                                <li class="slineDv">成立时间：{{n.createtime}}</li>
	                                <li class="slineDv">学校规模：{{n.schooltotalstudent}}</li>
	                                <li class="slineDv">国际学生：{{n.interstudent}}</li>
	                            </ul>
	                        <!-- </a> -->
	                        </router-link>
	                    </div>
	                    <div class="btn-wapper left">
                            <router-link :to="{ name: 'Schooldetails', params: { id: n.id }}"  class="admissions">领取官方招生手册</router-link>
                            <router-link :to="{ name: 'Schooldetails', params: { id: n.id }}"  class="subscribe">招生官面试预约</router-link>
                            <router-link to="/Application"  class="apply">官方免费申请</router-link>
	                    </div>
	                </li>
	            </ul>
	            <div id="jqPaginator" class="pageNav" style="display: block;"></div>
	        </div>
	    </div>
	</div>
</template>
<script>
	import headerTop from '@/pages/common/header.vue';
	
	export default{
		name: 'indexSenior',
		data() {
			return {
				nav: 'school',
	            area: [],
	            city: [],
	            request:{
	                page:1,
	                limit:10,
                    category: '4',
                    name: '',
                    area: '',
                    attribute: ''
	                // school_type:1,
	                // xingzhi_name:"",
	                // rank:"",
	                // nowcid:"",
	                // schoolname:""
	            },
                // schoolCategory: '',
	            count:"",
	            lists:[],
	            tuition: [{
	                name: "60万円以下",
	                id: "1"
	            }, {
	                name: "60-70万円",
	                id: "2"
	            }, {
	                name: "70-80万円",
	                id: "3"
	            }, {
	                name: "80万円以上",
	                id: "4"
	            }]
			}
        },
        components: {
			headerTop
		},
        filters:{
            filterfee: function(val){
                if (val) {
                    val = parseFloat(val/10000);
                    val = val.toFixed(2);
                }
                return val;
            }
        },
        methods: {
            getarea: function(_id) {
                var _data = {};
                if (_id) {
                    _data = {
                        id : _id
                    }
                }
                var _this = this;
                $.ajax({
                    url:"http://manage.xiaoying.net/getarea",
                    type:"post",
                    data:_data,
                    success:function(res){
                        if (res.status) {
                            if (!_id) {
                                _this.area = res.data;
                            }else{
                                _this.city = res.data;
                            }
                            _this.$nextTick(function(){
                                $("#city").select2({
                                    width: "300px",
                                })
                            })
                        }
                    }
                })
            },
            change: function(area,val){
                this.request[area] = val;
                this.request.page = 1;
                this.getdata();
            },
            getdata: function(){
                var _this = this;
                $.ajax({
                    /*category:
                    name:
                    area：
                    page:
                    limit:
                    attribute:
                    */
                    url:"http://manage.xiaoying.net/yuanxiao/api/index" ,
                    // url:"http://manage.xiaoying.net/getlangschool",
                    type:"get",
                    data: _this.request,
                    success:function(res){
                        if (res.code == 0) {
                            var arr = res.data;
                            // for (var i = 0; i < res.data.length; i++) {
                            //     if (res.data[i].name != "晓星国际学园") {
                            //         arr.push(res.data[i])
                            //     }
                            // }
                            _this.area = res.more.allregion;
                            // _this.schoolCategory = res.more.category;
                            _this.lists = arr;
                            _this.count = res.count;
                            
                            
                            $("#jqPaginator").html("");
                            if (res.count > 0) {
                                $('#jqPaginator').jqPaginator({
                                    totalCounts: parseInt(res.count),
                                    pageSize: _this.request.limit,
                                    visiblePages: 7,
                                    currentPage: _this.request.page,
                                    first: "<a>首页</a>",
                                    last: "<a>末页</a>",
                                    prev: "<a>上一页</a>",
                                    page: "<a class='page'>{{page}}</a>",
                                    next: "<a>下一页</a>",
                                    onPageChange: function(num, type) {
                                        if (type == "change") {
                                            _this.request.page = num;
                                            _this.getdata();
                                            $("body, html").animate({
                                                scrollTop: $(".resultPart").offset().top - 150
                                            }, 200)
                                        }
                                    }
                                })
                            }
                        }
                        // console.log("xxx",res);
                    }
                })
            }
        },
        mounted: function() {
            var _this = this;
            // this.getarea();

            this.getdata();

            $(".choose .item ").click(function(){
                $(this).addClass("active").siblings().removeClass("active");
            })
            $("#area,#city").select2({
                width: "300px",
            })
            // $(".selectcity .city_item").eq(0).addClass("active")
            _this.$nextTick(function () {
                 $("#area").on("select2:select",function(e){
                    _this.change('area',$("#area").val());
                    console.log(111,$("#area").val());

                    // _this.getarea($("#area").val());
                });
                // $("#city").on("select2:select",function(e){
                //     _this.change('nowcid',$("#city").val());
                // });
            })
           
        }
		
	}
</script>
<style scoped lang="less">
@import '../../../static/stylesheet/pages/schoolPublic.css';
.resultPart {
	padding-top: 30px;
}
</style>



