<template>
	<div>
		<header-top :nav ="nav"></header-top>
		<div class="mainPart" id="detailsKr">
			<div class="topDeatailPart">
				<div class="cover_banner_part"></div>
				<div class="container maininfo clearfix">
					<div class="left leftPart">
						<div class="imgDv">
							<img :src="tempimg" alt="" data-adaptive-background='1' data-description='grandpa' class="imgs">
						</div>
					</div>
					<div class="right rightPart">
						<p class="name">{{detail.name}}</p>
						<a :href="detail.weburl" class="url_href" target="_blank">{{detail.weburl}}</a>
						<ul class="listinfo clearfix">
							<li class="itemli clearfix">
	                            <span class="s_title">学校类型：</span>
	                            <span class="s_content">{{detail.category?detail.category:"暂无"}}</span>
	                        </li>
	                        <li class="itemli clearfix">
	                            <span class="s_title">学校性质：</span>
	                            <span class="s_content">{{detail.attribute?detail.attribute:"暂无"}}</span>
	                        </li>
	                        <li class="itemli clearfix">
	                            <span class="s_title">所在地区：</span>
	                            <span class="s_content">{{detail.area?detail.area:"暂无"}}</span>
	                        </li>
	                        <li class="itemli clearfix">
	                            <span class="s_title">受关注度：</span>
	                            <span class="s_content">{{detail.likenumber?detail.likenumber:"暂无"}}</span>
	                        </li>
	                        <li class="itemli clearfix">
	                            <span class="s_title">世界排名：</span>
	                            <span class="s_content">{{detail.worldrank?detail.worldrank:"暂无"}}</span>
	                        </li>
	                        <li class="itemli clearfix">
	                            <span class="s_title">本国排名：</span>
	                            <span class="s_content">{{detail.rank?detail.rank:"暂无"}}</span>
	                        </li>
						</ul>
					</div>
				</div>
			</div>
			<div class="chooseulDv">
				<ul class="chooseul container">
					<li class="item_li active" data-flag="tab1">学校介绍</li>
	        		<li class="item_li" data-flag="tab6" v-if="!major">招生信息</li>
					<li class="item_li" data-flag="tab2" v-if="major">学校专业</li>
					<li class="item_li" data-flag="tab3">学校图库</li>
					<li class="item_li" data-flag="tab4">地理交通</li>
					<li class="item_li" data-flag="tab5">留学攻略</li>
				</ul>
			</div>
			<div class="mainDv">
				<div class="container clearfix detailInfo">
					<div class="left leftDv">
						
						<div class="tabListDv">
							<div class="maincontent" id="tab1">
								<div class="mm_main" v-if="detail.contentchina != null && detail.contentchina != ''">
									<!-- <div class="mm_header">
										<h3>学校简介</h3>
									</div> -->
									<div class="mm_content"></div>
								</div>
							</div>
							<div class="maincontent" id="tab6">
		                    	<div class="mm_main" v-if="detail.enrollmentdesc != null && detail.enrollmentdesc != ''">
		                            <!-- <div class="mm_header">
		                                <h3>招生信息</h3>
		                            </div> -->
		                            <div class="mm_content"></div>
		                            <!-- <div class="mm_content" v-html="detail.enrollmentdesc"></div> -->
		                        </div>
		                        <div v-else>
		                        	暂无
		                        </div>
		                    </div>
							<div class="maincontent" id="tab2">
								<div class="xuebuZhuanYeDv clearfix" v-for = "(n,index) in major" :key="index" v-if = "major">
									<div class="mm_header">
		                                <h3>{{index}}</h3>
		                            </div>
									<ul class="zzzhuanye clearfix"> 
										<li class="zzzhuanyeItm" v-for="item in n">
										 	<div class="mname">{{item.mname}}<i class="icoa"></i>
										 	</div> 
										 	<div class="zzzhuanyetableDv" style="display: none;"> 
										 		<table class="zzzhuanyetable"> <tbody>
										 			<!-- <tr> 
										 				<td class="i_title">摘&nbsp;&nbsp;&nbsp;要</td>
										 				<td v-html="item.outline"></td>
										 			</tr>  -->
										 			<tr> 
										 				<td class="i_title">定&nbsp;&nbsp;&nbsp;員</td>
										 				<td v-html="item.quota"></td>
										 			</tr> 
										 			<tr> 
										 				<td class="i_title">年&nbsp;&nbsp;&nbsp;限</td> 
										 				<td v-html="item.term"></td> 
										 			</tr> 
										 			<tr> 
										 				<td class="i_title">学&nbsp;&nbsp;&nbsp;費</td> 
										 				<td v-html="item.majorfee"></td> 
										 			</tr>
										 			<!-- <tr> 
										 				<td class="i_title">对&nbsp;&nbsp;&nbsp;象</td> 
										 				<td>男女</td> 
										 			</tr>  -->
										 		</tbody>
										 	</table> 
										 </div> 
										</li> 
									</ul> 
								</div>
								<div class="xuebuZhuanYeDv clearfix" v-for = "n in major" v-else>
									<p>暂无该学校学部专业</p>
								</div>
								<div id="jqPaginator" class="pageNav" style="display: block;"></div>
							</div>
							<div class="maincontent" id="tab3">
								<!-- <div class="schoolCircle">
									<p>{{detail.name}}</p>
									<span>校区图片</span>
								</div>
								<div v-if="detail.allpic && detail.allpic.length > 0">
									<ul class="imgUl">
										<li class="img_li clearfix" v-for="(n,index) in detail.allpic">
											<div class="imgDv">
												<img :src="'http://manage.xiaoying.net'+n" alt="">
												<span class="arrow"></span>
											</div>
											<div class="numDv">
												<p>{{index+1}}</p>
												<span class="circle"></span>
											</div>
										</li>
										<li class="border_li"></li>
									</ul>
								</div> -->
								<ul class="imgUl" v-if="detail.images && detail.images.length > 0">
		                        	<li  v-for="(n,index) in detail.images">
		                        		<img :src="'http://manage.xiaoying.net/Uploads/resources/school/'+n.imagepath" alt="" >
		                        	</li>
		                        </ul>
		                        <div>
		                        	暂无
		                        </div>
								
							</div>
							<div class="maincontent" id="tab4">
								<div class="mm_main">
									<div class="mm_header">
										<h3>地理交通</h3>
									</div>
									<div class="smapDv" id="baiduMap" v-show="detail.longitude && detail.latitude"></div>
									
								</div>
							</div>
							<div class="maincontent" id="tab5">
								<ul class="newsListUl">
									<li v-for = "item in newsList" :key="item.id">
										<div class="imgDv">
		                                	<router-link :to = "{name: 'Newsdetails',params: {id: item.id}}">
		                                        <img :src="item.thumb" :alt="item.title">
		                                	</router-link>
		                                </div>
		                                <div class="contDv">
	                                    	<router-link :to = "{name: 'Newsdetails',params: {id: item.id}}">
			                                    <h4 style="width: 482px;">
			                                    	{{item.title}}
			                                    </h4>
		                                    </router-link>
		                                    <p class="time"><span></span>{{item.publishedtime}}</p>

	                                    	<router-link :to = "{name: 'Newsdetails',params: {id: item.id}}">
			                                    <p class="newsDes"  v-html="item.des">
			                                    	{{item.des}}
			                                    </p>
	                                    	</router-link>
		                                    <div class="btnPart">
		                                    	<router-link :to = "{name: 'Newsdetails',params: {id: item.id}}">查看详情</router-link>
		                                        <ul class="clearfix">
		                                            <li>
		                                                <span></span>{{item.upsnum}}
		                                            </li>
		                                            <li>
		                                                <span></span>{{item.hits}}
		                                            </li>
		                                        </ul>
		                                    </div>
		                                </div>
									</li>
								</ul>
							</div>
						</div>
					</div>
					<div class="right rightDv">
						<quick-consult></quick-consult>
	                	<apply-students :schoolname="schoolname" v-if="schoolname&&isShowApplyStatus"></apply-students>
	                	<same-city :area="area" v-if="isShowSameCity&&area"></same-city>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import headerTop from '@/pages/common/header.vue';
	import quickConsult from '@/pages/school/components/quickConsult.vue';
	import applyStudents from '@/pages/school/components/applyStudents.vue';
	import sameCity from '@/pages/school/components/sameCity.vue';
	export default{
		name: 'Schooldetailsjpcoll',
		data() {
			return {
				nav: 'school',
				area: '',
				isShowSameCity: '',
				schoolname: '',
	        	isShowApplyStatus: '',
				//专业
				cate:[],
	            //学历
	            xueli:[],
	            request:{
	            	type:"",
	            	cate:"",
	            	name:"",
	            	id:"",
	            	page:1,
	            	limit:10
	            },
	            newsList: [],
	            major:[],
	            tempimg:"",
	            detail:{},
	            schoolId: '',
	        	countryId: ''


	        }
	    },
	    components: {
	    	headerTop,
	    	quickConsult,
			applyStudents,
			sameCity

	    },
	    filters:{
	    },
	    watch: {
		    '$route' (to, from,next) {
		    	var _this = this;
	        	var _s = Math.round(Math.random()*5+1);
	        	this.tempimg = '../../../static/images/school/school/random'+_s+'.jpg';
	        	$(".cover_banner_part").backgroundBlur({
	        		imageURL : '../../../static/images/school/school/random'+_s+'.jpg',
	        		blurAmount : 50,
	        		duration: 1000,
	        		endOpacity : 1
	        	});
	        	_this.schoolId = _this.$route.params.id;

	        	this.getdata(_this.schoolId);
	            this.request.id = _this.schoolId;
			   this.getzhuanye(_this.schoolId);
			    _this.getCountry();

			   $(document).on('click', '.choose .item', function() {
			   	$(this).addClass("active").siblings().removeClass("active");
			   })
			   $('.chooseul .item_li').each(function () {
	            	if ($(this).hasClass('active')) {
	            		$(this).addClass("active").siblings().removeClass("active");
	                	var _flag = $(this).attr("data-flag");
	                	// console.log(_flag)
	            		$("#"+_flag).show().siblings().hide();
	            	}
	            })
			   $(document).on('click', '.chooseul .item_li', function() {
			   	$(this).addClass("active").siblings().removeClass("active");
			   	var _flag = $(this).attr("data-flag");

			   	$("#"+_flag).show().siblings().hide();
			   	if ($(this).data('flag') == 'tab4') {
			   		_this.initmap();
			   	}
			   })
		    }
	    },
	    methods: {
	    	escapeStringHTML:function(str) {
	    		str = str.replace(/&lt;/g,'<');
	    		str = str.replace(/&gt;/g,'>');
	    		str = str.replace(/&amp;/g,'&');
	    		str = str.replace(/&quot;/g,'"');
	    		str = str.replace(/&#039;/g,"'");
	    		return str;
	    	},
	    	isShowApply (schoolname) {
				var _this = this;
				$.ajax({
					url:"http://manage.xiaoying.net/mantoucase/getsamecasebyschoolname?name=" + schoolname,
                    type:"get",
                    success: function(res) {
                    	// console.log(res)
        				if (res.length >0) {
        					_this.isShowApplyStatus = true;
        				} else {
        					_this.isShowApplyStatus = false;
        				}
        				
                    }
				})
			},
			isShowSame (area) {
				var _this = this;
				$.ajax({
					url:"http://manage.xiaoying.net/mantou/getsameschoolbyarea?area=" + area,
                    type:"get",
                    success: function(res) {
        				if (res.length >0) {
        					_this.isShowSameCity = true;
        				} else {
        					_this.isShowSameCity = false;
        				}
                    }
				})
			},
            //获取专业分类
            getcate: function(){
            	var _this = this;
            	$.ajax({
            		url:"http://manage.xiaoying.net/school/cate",
            		type:"get",
            		success:function(res){
            			if (res) {
            				_this.cate = res
            			}
            		}
            	})
            },
            //获取学历
            getxueli: function(){
            	var _this = this;
            	$.ajax({
            		url:"http://manage.xiaoying.net/school/xueli",
            		type:"get",
            		success:function(res){
            			if (res) {
            				_this.xueli = res
            			}
            		}
            	})
            },
            //获取专业列表
            getzhuanye:function(_id){
            	var _this = this;
            	// this.major = [];
            	$.ajax({
            		url:"http://manage.xiaoying.net/yuanxiao/api/detail",
            		// url:"http://manage.xiaoying.net/school/schoolprogram",
            		type:"get",
            		data: {
            			yuanxiao_id:_id,
            			page: _this.request.page,
            			limit: _this.request.limit
            		},
            		// data:_this.request,
            		success:function(res){
            			$("#jqPaginator").html("");
            				if (res.data.majorcount > _this.request.limit && parseInt(parseInt(res.data.majorcount)/parseInt(_this.request.limit))>0) {
            					$('#jqPaginator').jqPaginator({
            						totalCounts: parseInt(res.data.majorcount),
            						pageSize: _this.request.limit,
            						visiblePages: 7,
            						currentPage: _this.request.page,
            						first: "<a>首页</a>",
            						last: "<a>末页</a>",
            						prev: "<a>上一页</a>",
            						page: "<a class='page'>{{page}}</a>",
            						next: "<a>下一页</a>",
            						onPageChange: function(num, type) {
            							if (type == "change") {
            								_this.request.page = num;
            								_this.getzhuanye(_id);
            							}
            						}
            					})
            				}

            				_this.major = res.data.catemajor;
            				// console.log('major',_this.major)
            				_this.$nextTick(function () {
            					$(".zzzhuanyeItm .mname").click(function(){
									let parent = $(this).parent(),
										table = parent.find(".zzzhuanyetableDv");
									if (table.css("display") == "none") {
										table.slideDown();
										parent.addClass('active');
									}else{
										table.slideUp();
										parent.removeClass('active');
									}
								})
            				})
            				
            		}
            	})
            },
            //获取咨询
            getNewListData:function (_id) {
                var _this = this;
                $.ajax({
                   url:"http://manage.xiaoying.net/article/contentssearch?limit=7&countryid=" + _id,
                    // data:{limit:5},
                    type:"get",
                    success: function(res) {
                        var array = res.data;
                        // 'http://www.xiao-ying.net'+item.thumb
                        for (var i = 0; i < array.length; i++) {
                            // if(array[i].thumb.indexOf('http') > -1) {
                            //     array[i].thumb = array[i].thumb;
                            // }else if(array[i].thumb == '') {
                            //     array[i].thumb = '../../../static/images/news/img.jpg'
                            // } else {
                            //     array[i].thumb = 'http://manage.xiaoying.net'+ array[i].thumb;
                            // }

                            if(array[i].thumb.indexOf('http') > -1) {
								array[i].thumb = array[i].thumb;
							}else if(array[i].thumb == '') {
								array[i].thumb = '../../../static/images/news/img.jpg'
							}else if (array[i].thumb.indexOf('ueditor/php/upload/image') > -1) {

								array[i].thumb = 'http://manage.xiaoying.net'+ array[i].thumb;

							}else if(array[i].thumb.indexOf('files/defaul') > -1){
								array[i].thumb = 'http://www.xiao-ying.net/'+ array[i].thumb;
							}else if(array[i].thumb.indexOf('Public/blog') > -1){
								var _src = array[i].thumb.replace('Public/blog','files/blog');

								array[i].thumb = 'http://www.xiao-ying.net/' +_src;
							}else {
								array[i].thumb = 'http://manage.xiaoying.net'+ array[i].thumb;
							}
							
                            array[i].des = _this.escapeStringHTML(array[i].des);
                            array[i].publishedtime = _this.timestampToTime(array[i].publishedtime);

                        }
                        _this.newsList = array;
                    }
                })
            },
            change: function(key,val){
            	this.request[key] = val;
            	this.getzhuanye();
            },
            getdata: function(_id){
            	var _this = this;
            	$.ajax({
            		url:"http://manage.xiaoying.net/yuanxiao/api/detail",
            		type:"get",
            		data: {yuanxiao_id:_id},
            		success:function(res){
            			
            				_this.detail = res.data;
            				_this.schoolname = res.data.name;
                            _this.isShowApply(_this.schoolname);
                            _this.area = res.data.area;
                            _this.isShowSame(_this.area);
                            _this.detail.contentchina = _this.detail.contentchina? _this.detail.contentchina:_this.detail.content;

            				if (_this.detail.contentchina) {
            					_this.detail.contentchina = _this.escapeStringHTML(_this.detail.contentchina)
            					
            					var $pre = $('<pre>');
                                $pre.html(_this.detail.contentchina);
                                _this.$nextTick(function () {
                                	$('#tab1 .mm_content').html($pre.html());
                                	$('#tab1 .mm_content h2').css({
                                		"font-size": "20px",
                                		"color":" #000000",
                                		"line-height": "24px",
                                		"border-left": "5px solid  #36c2a1",
                                		"padding-left": "10px",
                                		"margin": "10px 0"
                                	});

                                })
            				} else {
            					_this.detail.contentchina = "暂无";
            				}
            				if (_this.detail.enrollmentdesc) {
                                _this.detail.enrollmentdesc = _this.escapeStringHTML(res.data.enrollmentdesc);
                                var $pre1 = $('<pre>');
                                $pre1.html(_this.detail.enrollmentdesc);
                                _this.$nextTick(function () {
                                	$('#tab6 .mm_content').html($pre1.html());
                                	$('#tab6 .mm_content strong').css({"font-weight": "700"})
                                	$('.schoolcontent h2').css({
                                		"font-size": "20px",
                                		"color":" #000000",
                                		"line-height": "24px",
                                		"border-left": "5px solid  #36c2a1",
                                		"padding-left": "10px",
                                		"margin": "10px 0"
                                	});
                                })
                            }

            				if (_this.detail.longitude && _this.detail.latitude) {
            					_this.detail.longitude = parseFloat(_this.detail.longitude);
            					_this.detail.latitude = parseFloat(_this.detail.latitude);
            					// _this.initmap();
            				}
            			
            		}
            	})
            },
            initmap:function(){
            	var _this = this;
            	var _point = [_this.detail.longitude,_this.detail.latitude];
            	$("#baiduMap").show();
                // 百度地图API功能
                var map = new BMap.Map("baiduMap", {});
                map.enableScrollWheelZoom(true);
                var point = new BMap.Point(_point[0], _point[1]);
                map.centerAndZoom(point, 50);
                
                //创建logo
                var pt = new BMap.Point(_point[0], _point[1]);
                var myIcon = new BMap.Icon("../../../static/images/school/mappoint.png", new BMap.Size(95, 36));
                var marker2 = new BMap.Marker(pt, { icon: myIcon }); // 创建标注
                map.addOverlay(marker2);
            },
            getQueryString: function(name, needdecoed) {
            	var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
            	var lh = window.location.search;
            	if (needdecoed) {
            		lh = decodeURI(window.location.search)
            	}
            	var r = lh.substr(1).match(reg);
            	if (r != null) return unescape(r[2]);
            	return null;
            },
            timestampToTime:function(timestamp) {
                var date = new Date(timestamp * 1000);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
                var Y = date.getFullYear() + '-';
                var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
                var D = date.getDate() + ' ';
                var h = date.getHours() + ':';
                var m = date.getMinutes() + ':';
                var s = date.getSeconds();
                return Y+M+D;
                // return Y+M+D+h+m+s;
            },
            getCountry() {
				var _this = this;
				$.ajax({
					url: "http://manage.xiaoying.net/getcountry",
					type:"GET",
					success: function (res) {
						if(res.status) {
							for (var i = 0; i < res.data.length; i++) {
								if (res.data[i].name == '馒头留学') {
									_this.countryId = res.data[i].id;
           							_this.getNewListData(_this.countryId);
									
								}
							}
						}
					}
				})
			}
        },
        mounted: function() {
        	var _this = this;
        	var _s = Math.round(Math.random()*5+1);
        	this.tempimg = '../../../static/images/school/school/random'+_s+'.jpg';
        	$(".cover_banner_part").backgroundBlur({
        		imageURL : '../../../static/images/school/school/random'+_s+'.jpg',
        		blurAmount : 50,
        		duration: 1000,
        		endOpacity : 1
        	});
        	_this.schoolId = _this.$route.params.id;

        	this.getdata(_this.schoolId);
            this.request.id = _this.schoolId;
		   this.getzhuanye(_this.schoolId);
		    _this.getCountry();

		   $(document).on('click', '.choose .item', function() {
		   	$(this).addClass("active").siblings().removeClass("active");
		   })
		   $('.chooseul .item_li').each(function () {
            	if ($(this).hasClass('active')) {
            		$(this).addClass("active").siblings().removeClass("active");
                	var _flag = $(this).attr("data-flag");
                	// console.log(_flag)
            		$("#"+_flag).show().siblings().hide();
            	}
            })
		   $(document).on('click', '.chooseul .item_li', function() {
		   	$(this).addClass("active").siblings().removeClass("active");
		   	var _flag = $(this).attr("data-flag");

		   	$("#"+_flag).show().siblings().hide();
		   	if ($(this).data('flag') == 'tab4') {
		   		_this.initmap();
		   	}
		   })
		  
		}

}
</script>
<style scoped lang="less">
@import '../../../static/stylesheet/pages/detailsKr.css';
</style>



