<template>
	<div>
		<div class="hotFlag">
			<h4>热门标签</h4>
			<ul class="hotFlagList clearfix">
				<li v-for="n in hotflag" :key="n.tagname">
					<router-link :to="{name: 'Hottags',params: {id: n.id}}" @click="clickTags(n.tagname)">
						<!-- <a href="javascript:;" @click="clickTags(n.tagname)"> -->
							
					{{n.tagname}}
						<!-- </a> -->
					</router-link>
				</li>
				
			</ul>
		</div>
	</div>
</template>
<script>
	export default{
		name: 'hotFlag',
		data () {
			return {

			}
			
		},
		props: {
			hotflag: Array
		},
		methods: {
			clickTags: function (tagname) {
				localStorage.hottags = tagname;
			}
		},
		mounted () {

		}
	}
</script>
<style scoped lang="less">
@import url("../../../static/stylesheet/function.less");
@import url("../../../static/stylesheet/publicConfig.less");
.hotFlag {
	background-color: #fff;
	.border-radius(4px);
	padding: 6px 20px 40px;
	margin-bottom: 20px;
	h4 {
		font-size: 16px;
		color: #262626;
		line-height: 44px;
		border-bottom: 1px solid @themeColor;
	}
	.hotFlagList {
		padding-top: 9px;
		li {
			float: left;
			font-size: 14px;
			color: #3a3a3a;
			line-height: 30px;
			padding: 0 10px;
			.border-radius(4px);
			background-color: @themeColor;
			margin-right: 12px;
			margin-top: 12px;
			a {
				color: #fff;
			}
		}
	}
}

</style>


