<template>
	<div>
		<header-top :nav ="nav"></header-top>
		<div class="mainPart">
			<div class="bannerPart">
				
			</div>
			<div class="newsNavPart">
				<ul class="container newsNavUl">
					<li @click="clickNewsNav" data-cateid="newsNavAll"><a href="javascript:;">全部</a></li>
					<li v-for="n in newsNav" @click="clickNewsNav" :data-cateid="n.id" :key = "n.id">
						<a href="javascriot:;">{{n.name}}</a>
					</li>
				</ul>
			</div>
			<div class="container clearfix contentPart">
				<div class="leftPart">
					<div class="newsContPart">
						<div class="newsdetailsContPart">

							<h4 class="currentAddress">您当前的位置：<router-link to="/News">留学资讯</router-link>>><router-link to="/News">全部</router-link>>><span>正文详情</span></h4>
							<div class="detailsTitlePart">
								<h3 class="detailsTitle">{{newsDetailInfo.title}}</h3>
							</div>
							<div class="newsSourcePart clearfix">
								<ul class="sourceUl clearfix">
									<li class="sourse">{{newsDetailInfo.source}}</li>
									<!-- <li class="editor">xiaoying</li> -->
									<li class="time">{{newsDetailInfo.publishedtime}}</li>
								</ul>
								<ul class="eyeUl clearfix">
									<li class="thumb" @click="clickThumb">
										<span></span>{{vote}}
									</li>
									<li class="eye">
										<span></span>{{reads}}
									</li>
									<!-- <li class="share">
										<span></span>18
									</li> -->
								</ul>
							</div>
							<div class="detailsContPart">
								<p class="infoBody" v-html="newsDetailInfo.body"></p>
								<div class="thumbPart"  @click="clickThumb">
									<div>
										<img src="../../../static/images/news/thumb.png">
										<p>赞</p>
									</div>
								</div>
								<p class="likeNum">{{vote}}人赞过</p>
							</div>
							<div class="labelPart" v-if ="hotflag.length>0">
								<div class="labelItem clearfix">
									<div class="labelTitle">你关心的标签: </div>
									<div class="labelUl">
										<ul class="clearfix">
											<li v-for="n in hotflag" :key="n.tagname">
												<router-link :to="{name: 'Hottags',params: {id: n.id}}" >
												<!-- <a href="javascript:;"> -->
													{{n.tagname}}
												<!-- </a> -->
												</router-link>
											</li>
										</ul>
									</div>
								</div>
								<!-- <div class="labelItem clearfix">
									<div class="labelTitle">我关心的标签: </div>
									<div class="labelUl">
										<ul class="clearfix">
											<li><a href="javascript:;">日本留学面试问题</a></li>
											<li><a href="javascript:;">东京大学</a></li>
											<li><a href="javascript:;">早稻田大学</a></li>
											<li><a href="javascript:;">日本大学排名</a></li>
											<li><a href="javascript:;">日本大学排名</a></li>
											<li><a href="javascript:;">日本大学排名</a></li>
											<li><a href="javascript:;">日本大学排名</a></li>
										</ul>
									</div>
								</div> -->
							</div>
							<p class="notice">声明：本站文章标明 [原创] 系本站原创内容，如需转载请注明</a>] 。任何单位或个人认为本站内容涉嫌侵犯其合法权益，请及时向本站书面反馈并提供证明，本站将会尽快移除被控侵权内容。</p>
						</div>

					</div>
				</div>
				<div class="rightPart">
					<hot-activities></hot-activities>
					<hot-flag :hotflag="hotflag" v-if ="hotflag.length>0"></hot-flag>
					<hot-news></hot-news>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import headerTop from '@/pages/common/header.vue';
	import hotActivities from '@/pages/common/hotActivities.vue';
	import hotNews from '@/pages/common/hotNews.vue';
	import hotFlag from '@/pages/common/hotFlag.vue';
	export default{
		name: 'Newsdetails',
		data () {
			return {
				nav: 'news',
				hotflag: '',
				newsNav: [],
				newsDetailInfo: {},
				vote: 0, //点赞数
				reads: 0, //阅读量
				countryId: 0
			}
		},
		components: {
			headerTop,
			hotActivities,
			hotNews,
			hotFlag
		},
		watch: {
		    '$route' (to, from,next) {
		      // 对路由变化作出响应...
		    	var _this = this;
				_this.getCountry();
				_this.getData();
				_this.setReads();
		    }
	    },
		methods: {
			// &rdquo;
			escapeStringHTML:function(str) {
			    str = str.replace(/&lt;/g,'<');
			    str = str.replace(/&gt;/g,'>');
			    str = str.replace(/&amp;/g,'&');
			    str = str.replace(/&quot;/g,'"');
			    str = str.replace(/&#039;/g,"'");
			    // str = str.replace(/&rdquo;/g,'"');
			    return str;
			},
			getNewsNavData (_id) {
				var _this = this;
				
				$.ajax({
					url: "http://manage.xiaoying.net/article/catesearch?countryid=" + _id,
					type:"GET",
					success: function(res) {
						_this.newsNav = res;

						_this.$nextTick(function () {
							// console.log(localStorage.cateid)
							$('.newsNavUl li').each(function () {
								if ($(this).data('cateid') == localStorage.cateid) {
								// console.log(11,$(this).data('cateid'))

									$(this).addClass('active').siblings().removeClass('active');
								}
							})
						})
					}
				})
			},
			clickNewsNav(event) {
				var _this= this;
				var el =  event.currentTarget;
				if ($(el).data('cateid') != 'newsNavAll') {
					localStorage.cateid = $(el).data('cateid');
				} else {
					localStorage.cateid = '';
				}
				localStorage.cateidSec = '';
				// console.log($(el).data('cateid'))
				_this.$router.push({ path: '/news' });
			},
			
			getData() {
				var _this = this;
				var id = _this.$route.params.id;
				$.ajax({
					url:"http://manage.xiaoying.net/article/detailsearch?id=" + id,
					type:"GET",
					success: function (res) {
						if (res.status) {
							var obj = res.data;
							obj.source = obj.source?obj.source:'网络';
							obj.publishedtime = _this.timestampToTime(obj.publishedtime);
							obj.body = _this.escapeStringHTML(obj.body);
							_this.newsDetailInfo = obj;

							_this.hotflag = obj.tags;

							_this.vote = obj.upsnum;
							_this.reads = obj.hits;
							// var $pre = $('<pre>');
							// $pre.html(_this.newsDetailInfo.body);
							// $('.infoBody').html($pre.text());
							_this.$nextTick(function () {
								$('.infoBody img').each(function () {
									if($(this).attr('src').indexOf('/ueditor') == 0) {
										$(this).attr('src','http://manage.xiaoying.net' + $(this).attr('src'));
									}
									$(this).css({'margin-top':'20px','max-width':' 100%'});
									// $(this).css({'margin-top':'20px'});
								})
								$('.infoBody strong').css('font-weight',700);
							})
							

							$("body, html").animate({
                                scrollTop: $(".newsNavPart").offset().top - 75
                            }, 800)
						}
					}
				})
			},
			timestampToTime (timestamp) {
		        var date = new Date(timestamp * 1000);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
		        var Y = date.getFullYear() + '-';
		        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
		        var D = date.getDate() + ' ';
		        var h = date.getHours() + ':';
		        var m = date.getMinutes() + ':';
		        var s = date.getSeconds();
		        return Y+M+D;
		        // return Y+M+D+h+m+s;
		    },
		    clickThumb () {
		    	var _this = this;
				var id = _this.$route.params.id;
		    	$.ajax({
					url:"http://manage.xiaoying.net/vote?id=" + id,
					type:"GET",
					success: function (res) {
						if (res.status) {
							_this.vote++
						}else {
							console.log(res.msg);
						}
					},
					error: function (res) {
						console.log(res.msg);
					}
				})
		    },
		    setReads() {
		    	var _this = this;
				var id = _this.$route.params.id;
				$.ajax({
					url:"http://manage.xiaoying.net/reads",
					type:"POST",
					data: {
						id: id
					},
					dataType: 'json',
					success: function (res) {
						if (res.status) {
							_this.reads++
						}else {
							console.log(res.msg);
						}
					},
					error: function (res) {
						console.log(res.msg);
					}
				})
		    },
		    getCountry() {
				var _this = this;
				$.ajax({
					url: "http://manage.xiaoying.net/getcountry",
					type:"GET",
					success: function (res) {
						if(res.status) {
							for (var i = 0; i < res.data.length; i++) {
								if (res.data[i].name == '馒头留学') {
									_this.countryId = res.data[i].id;
									// console.log('apply_getCountry',_this.countryId);

									//初始化一级分类
									_this.getNewsNavData(_this.countryId);
									
								}
							}
						}
					}
				})
			}
		},
		mounted () {
			var _this = this;
			_this.getCountry();

			_this.getData();
			_this.setReads();
			
		}
	}
</script>
<style scoped lang="less">
@import '../../../static/stylesheet/pages/newsdetails.css';

</style>



