<template>
	<div>
		<div class="school-common" id="school-samecity">
			<p>
				同城学校<span class="wwcitycount">（{{sameCityList.length}}个）</span>
			</p>
			<div class="wwcity" v-for= "n in sameCityList">
				<dl>
					<dd>
						<router-link :to="{ name: 'Schooldetails', params: { id: n.id }}">
							<img :src="'http://manage.xiaoying.net//Uploads/resources/school/' + n.defaultimage" :alt="n.name">
						</router-link>
					</dd>

				</dl>
				<dl>
					<dt>
						<router-link class="sTitle" :to="{ name: 'Schooldetails', params: { id: n.id }}">
							{{n.name}}
						</router-link>
					</dt>
					<dd>
						<router-link :to="{ name: 'Schooldetails', params: { id: n.id }}">
							<p class="des" v-html="n.des"></p>
						</router-link>
					</dd>
					
				</dl>
			</div>
			                       
			<div class="more-tag">
				<router-link class="btn-more" to="/School">查看更多</router-link>
			</div>
		</div>
	</div>
</template>
<script>
	export default{
		name: 'sameCity',
		data () {
			return {
				sameCityList: []
			}
		},
		props: {
			area: String
		},
		methods: {
			escapeStringHTML:function(str) {
	    		str = str.replace(/&lt;/g,'<');
	    		str = str.replace(/&gt;/g,'>');
	    		str = str.replace(/&amp;/g,'&');
	    		str = str.replace(/&quot;/g,'"');
	    		str = str.replace(/&#039;/g,"'");
	    		return str;
	    	},
			getData (area) {
				var _this = this;
				$.ajax({
					url:"http://manage.xiaoying.net/mantou/getsameschoolbyarea?area=" + area,
                    type:"get",
                    success: function(res) {

        					var array = res;
        					for (var i = 0; i < array.length; i++) {
        						array[i].des = _this.escapeStringHTML(array[i].des);
        					}
        					_this.sameCityList = array;
                    }
				})
			}
		},
		mounted () {
			// console.log(222,this.area)
			this.getData(this.area);
		}
	}
</script>
<style scoped lang="less">
@import url("../../../../static/stylesheet/function.less");
@import url("../../../../static/stylesheet/publicConfig.less");
.school-common {
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 20px 20px 23px;
    // width: 320px;
    // border: 1px #E5E5E5 solid;
    background: #FCFCFC;
}
.school-common p {
    padding-bottom: 10px;
    color: #333;
    font-size: 14px;
}
.school-common p span {
    font-size: 14px;
    color: @themeColor;;
}
.school-common .wwcity {
	border-bottom: solid 1px #E5E5E5;
	padding-bottom: 10px;
	padding-top: 10px;
	overflow: hidden
}
.school-common .wwcity dl {
	vertical-align: middle;
}
.school-common .wwcity dl:first-child {
	width: 30%
}

.school-common .wwcity dl:last-child {
	width: 70%
}

.school-common .wwcity:first-of-type {
	padding-top: 0
}

.school-common .wwcity:last-child {
	border-bottom: none
}

.school-common dl {
	float: left
}

.school-common dl dt {
	padding-left: 8px;
	font-size: 16px;
	font-weight: normal;
	color: #333
}

.school-common .wwcity:hover dl dt a {
	color: @themeColor;
}

.school-common dl dt a:hover span {
	color: #999
}

.school-common dl dt span,.school-common dl dd p {
	// display: block;
	color: #999;
	font-size: 12px;
	font-weight: normal;
	word-wrap: break-word;
	word-break: normal;
	margin-top: 8px;
}
.school-common .sTitle {
	display: inline-block;
	width: 100%;
	.single-overflow()
}
.school-common dl dd p.des {
	line-height: 18px;
	height: 36px;
	.multi-overflow(2);
	
};
.school-common dl dd img {
	width: 86px;
	height: 64px;
	padding: 1px;
	border: 1px #E5E5E5 solid
}

.school-common dl dd {
	width: 100%
}

.school-common dl dd p {
	padding-left: 8px;
	padding-bottom: 0
}

.school-common dl dd p span {
	color: @themeColor;
}

.school-common dl dd strong span {
	color: #f39800
}

.more-tag {
    width: 137px;
    margin: 10px auto auto
}

.more-tag a.btn-more {
    display: block;
    line-height: 36px;
    vertical-align: middle;
    text-align: center;
    border: solid 1px @themeColor1;
    color: @themeColor1;
    font-size: 16px;
    font-weight: normal;
    cursor: pointer;
    .border-radius(4px);
    .transition(all .2s);
}

.more-tag a.btn-more:hover {
    color: @themeColor;
    border-color: @themeColor;
}

</style>



