<template>
	<div>
		<header-top :nav ="nav"></header-top>
		<div class="mainPart" id="onlinemall">
	        <div class="banner">
	            
	        </div>
	        <div class="container choosepart">
	            <div class="lineDv clearfix">
	                <span class="s_title">热门排行：</span>
	                <ul class="listul">
	                    <li class="item">[研究生]研究生VIP帝大保录</li>
	                    <li class="item">[留学日语]N2-N1签约班</li>
	                    <li class="item">SGU英语修士2所申请(送1所=3所)</li>
	                </ul>
	            </div>
	            <div class="lineDv choose clearfix">
	                <span class="s_title">产品名称：</span>
	                <div class="inputDv">
	                    <input type="text" placeholder="请输入产品名称" v-model="requset.name" @keyup.13="change('name',requset.name)">
	                    <i class="search" @click="change('name',requset.name)"></i>
	                </div>
	            </div>
	            <div class="lineDv choose clearfix">
	                <span class="s_title">产品类别：</span>
	                <ul class="listul">
	                    <li class="item active" @click="change('cate','')">不限</li>
	                    <li class="item" @click="change('cate','')">留学培训</li>
	                    <li class="item" @click="change('cate','999')">留学申请</li>
	                    <li class="item" @click="change('cate','999')">添加服务</li>
	                </ul>
	            </div>
	        </div>
	        <div class="block_shop">
	            <div class="titleDv">
	                名师推荐定向服务
	                <br />
	                <span class="en_font">Teacher recommended orientation service</span>
	            </div>
	        </div>
	        <div class="listPart">
	            <ul class="container listDv" v-if="lists.length > 0">
	                <li class="itemli" v-for="n in lists">
	                    <p class="pro_name">{{n.name}}</p>
	                    <p class="pro_desc">{{n.description | filterdesc}}</p>
	                    <!-- <p class="price">￥{{n.suggested_price}}</p> -->
	                    <!-- <p class="price" onclick="_MEIQIA('showPanel')" style="margin-top: 30px;">立即咨询</p> -->
	                    <!-- <p class="price"  style="margin-top: 30px;">立即咨询</p> -->
	                    <router-link class="price"  style="margin-top: 30px;" to="/Application">立即咨询</router-link>
	                </li>
	            </ul>
	            <div class="nodate" v-else>
	                暂无数据
	            </div>
	            <div id="jqPaginator" class="pageNav mallPageNav" style="display: block;"></div>
	        </div>
	    </div>

	</div>
</template>
<script>
	import headerTop from '@/pages/common/header.vue';
	export default{
		name: 'Mall',
		data () {
			return {
				nav: 'mall',
				lists:[],
	            count:"",
	            requset:{
	                page_size: 12,
	                page:1,
	                cate:"",
	                name:""
	            }
			}
			
		},
		components: {
			headerTop
		},
		filters: {
             filterdesc: function(val){
                var _desc = val;
                if (val == '') {
                    _desc = '馒头留学自2010年开始为馒头学员提供全面的留学产品以及服务。'
                }
                return _desc;
             }
        },
        methods: {
            change(key,val){
                this.lists = [];
                this.requset.page = 1;
                this.requset[key] = val;
                this.getdata();
            },
            getdata(){
                var _this = this;
                $.ajax({
                    url:"http://xiaoying.net/api/getgoods",
                    // url:"/api/getgoods",
                    type:"post",
                    data: this.requset,
                    success:function(res){
                        if (res.status) {
                            _this.lists = res.data;
                            _this.count = res.count;
                            $("#jqPaginator").html("");
                            if (res.count > 0) {
                                $('#jqPaginator').jqPaginator({
                                    totalCounts: parseInt(res.count),
                                    pageSize: _this.requset.page_size,
                                    visiblePages: 7,
                                    currentPage: _this.requset.page,
                                    first: "<a>首页</a>",
                                    last: "<a>末页</a>",
                                    prev: "<a>上一页</a>",
                                    page: "<a class='page'>{{page}}</a>",
                                    next: "<a>下一页</a>",
                                    onPageChange: function(num, type) {
                                        if (type == "change") {
                                            _this.requset.page = num;
                                            _this.getdata();
                                        }
                                    }
                                })
                            }
                        }
                    }
                })
            }
        },
        mounted: function() {
            this.getdata();

            $("#area,#city").select2({
                width: "300px",
            })

            $(".selectcity .city_item").eq(0).addClass("active")

            $(document).on('click', '.listul .item', function() {
                $(this).addClass("active").siblings().removeClass("active");
            });

		    var config = {
		        reset: true,
		        delay: 'always',
		        mobile: true,
		    }
		    window.sr = new scrollReveal(config);
        }
	}
</script>

</script>
<style scoped lang="less">
@import '../../../static/stylesheet/pages/mall.css';

</style>



