		                                                低一些。</p>
		                                            <p class="redp">专门性留学生宿舍： 一般 有专 业的宿 舍公 司负 责 运营。 仅限留 学生 居 住，人 源 较 整齐，社 会关 系较
		                                                简单 。 租 金 一般 和学 校宿 舍价 格差 不多。</p>
