		                                                公布 的 数据，日本留 学生平均 每月需 138,000日 元（ 包含 学 费）。</p>
		                                            <br/>
		                                            <p class="redp">伙食费： 伙 食费 标 准因人而异， 自己 做饭 一定 比外 面买 着吃 会 便 宜很 多。正 常情 况下， 留 学
		                                                生每 月的 伙食 费 应 该在3 万—6万日 元不 等。</p>
		                                            <br/>
		                                            <p class="redp">交通费： 以东京 为例， 地铁成 人单程 票价 种 类有： 160 日元(起 步价1 -6 公里 内)、190 日
		                                                元、23 0日 元、2 70 日 元 和30 0日元。电 车(俗称 的 JR线) 一般的 票 价在130-210 日 元。留 学生 还 享有 学
		                                                生优惠 ，或者购 买 月 票。</p>
		                                        </div>
