	                        "location": "神奈川县",
	                        "hot": "95",
	                        "url": "http://lang.xiao-ying.net/edu",
	                        "logoUrl": "http://doc.eggelite.com//Resources/lang/logo/2018-03-22/5ab33c7784fe0.jpg"
	                    }
                    ],
	            ] 
	            
			}
		},
		components: {
			headerTop
		},
		methods: {
			gotoSearch: function (hotschoolname) {

				var _this = this;
				if(hotschoolname) {
					_this.$router.push({ name: 'Schoolall', params: { name: hotschoolname }})
				} else {
					alert("请输入学校名字");
				}
			},
			getcase:function(){
                var _this = this;
                $.ajax({
                    url:"http://crm.xiaoying.net?m=case&a=frontsearch",
                    data:{
                        rows:10
                    },
                    type:"post",
                    success:function(res){
                    	// var arr = res.data.list;
                    	// for (var i = 0; i < arr.length; i++) {
                    	// 	arr[i].names = res.data.list[i].names.slice(0,1) + '同学';
                    	// }
                     //    _this.caselist = arr;

                     	//处理图片
                        var array = res.data.list;
                        for (var i = 0; i < res.data.list.length; i++) {
                            if(res.data.list[i].pic) {
                                array[i].pic = 'http://crm.xiaoying.net/' + res.data.list[i].pic;
                            } else {
                                var ramand = _this.randomNum(1,22);
                                array[i].r_pic = ramand;
                                array[i].pic = '../../../static/images/case/ramandImg/'+array[i].r_pic+'.jpg';
                            }
                            array[i].names = res.data.list[i].names.slice(0,1) + '同学';
                        }
                        _this.caselist = array;
                        _this.$nextTick(function(){
                            var caseSwiper = new Swiper('.swiper-case',{
							    pagination: '.pagination-case',
							    paginationClickable: true,
							    slidesPerView:3,
								loop:true,
								autoplay: 5000,
								//Enable 3D Flow
								tdFlow: {
									rotate : 10,
									stretch :-40,
									depth: 100,
									modifier : 0.2,
									shadows:false
								}
							})
                        });
                    }
                })
            },
            randomNum: function (minNum,maxNum) {
                switch(arguments.length){ 
                    case 1: 
                        return parseInt(Math.random()*minNum+1,10); 
                    break; 
                    case 2: 
                        return parseInt(Math.random()*(maxNum-minNum+1)+minNum,10); 
                    break; 
                        default: 
                            return 0; 
                        break; 
                } 
            },
            getBanner: function () {
            	var _this = this;
				$.ajax({
					url:"http://manage.xiaoying.net/getrecom?recom_id=22",
                    type:"get",
                    success: function(res) {
                        _this.getBannerList = res.recomdata;
                        _this.$nextTick(function () {
                        	//banner
                        	if (_this.getBannerList.length >1) {
                        		var swiperBanner = new Swiper('.swiper-container-index-banner',{
								    pagination: '.pagination-index-banner',
								    loop:true,
								    autoplay: 5000,
								    paginationClickable: true
								})

								$('.swiper-container-index-banner .swiper-slide').each(function () {

									$(this).find('a').css({
										"background": "url('"+$(this).find('img').attr('src')+"') no-repeat center center",

									})
								})
                        	}
                        })
                    }
				})
            },
            getHotMews: function () {
            	//热门资讯
            	var _this = this;
            	$.ajax({
					url:"http://manage.xiaoying.net/mantouarticle/contentssearch?countryid=3",
                    type:"get",
                    success: function(res) {
                       	if (res.data.length>6) {
                       		_this.hotNewsList = res.data.splice(0,6);
                       	} else {
                        	_this.hotNewsList = res.data;
                       	}
                    }
				})
            }
		},
		mounted() {
			var _this = this;
			_this.getBanner();
			_this.getHotMews();
			//banner form 
			$.getScript("http://crm.xiaoying.net/Public/js/xiaoying_form.js",function(){
				
				$(".xy_form_index").xiaoyingForm({
					parameter: [
					{errorMes: "姓名格式不正确", id: "XY_a01", rename: "姓名"},
		            {errorMes: "电话格式不正确", id: "XY_a02", rename: "手机"},
		            {errorMes: "微信格式不正确", id: "XY_b11", reg: "required"},
		            {errorMes: "请选择日语能力等级", id: "XY_b12", rename: "日语水平", reg: "required"},
		            {errorMes: "请选择留学时间", id: "XY_b09", reg: "required"},
		            {errorMes: "请填写所在年级", id: "XY_b19", reg: "required"},
		            {errorMes: "请选择所在城市", id: "XY_b13", reg: "required"},
					],
					submitBtn: ".xy_submit_index",
					beforeSendData: function(_data, cb) {
						cb(_data);
					},
					beforeLoadField: function(res) {
						return res;
					},
					afterLoadField: function() {
						$(".formpartDv select").select2({
							minimumResultsForSearch: -1
						});

						function formatState(state) {
							if (!state.title) {
								return state.text;
							}
							var baseUrl = "/Public/Common/img/nation";
							var $state = $(
								'<span><img src="' + baseUrl + '/' + state.element.title + '.png" class="img-flag" /> ' + state.text + '</span>'
								);
							return $state;
						};
					},
					validatorError: function(mes, node) {
						alert(mes);
					},
					submitSuccess: function() {
						alert("申请成功！请等到老师与您联系");
					},
					submitError: function() {}
				});

			});


			
			//hotSchool
		  	var swiperHotSchool = new Swiper('.swiper-container-hotSchool',{
			    pagination: '.pagination-hotSchool',
			    // loop:true,
			    // autoplay: 5000,
			    paginationClickable: true
			})
		    
		  	//authoration
		  	new Swiper('.swiper-shouquan', {
                // pagination: '.team-swiper-pagination',
                slidesPerView: 7,
                paginationClickable: true,
				loop:true,
                autoplay: 3000
            });
		    //case
		    _this.getcase();
		    
		
		    //honor
		    var honorSwiper = new Swiper('#swiper-honor',{
			    // pagination: '.pagination4',
			    slidesPerView: 3,
			    loop:true,
			    // autoplay: 2000,
			    autoplayDisableOnInteraction: false,
			    grabCursor: true,
			    paginationClickable: true,

			})
		    //address
		    // $(".addressHead .cityname").click(function() {
		    // 	$(".x_add_list").hide();
		    // 	var _flag = $(this).data("city");
		    // 	$(".x_add_list." + _flag).fadeIn();
		    //     $(this).addClass("active").siblings().removeClass("active");
		    //     $('.addressHead').animate({ scrollLeft: $(this).index() * 110 }, 400);
		    // });

		 
		    // var $w_show = $('.showDv').outerWidth() + 60;
		    var $w_show = 254;

		    $(".imgul .acrionDc").mouseenter(function () {
		    	var tl;
		    	$(".showDv").fadeOut();
		    	if (typeof(tl) == "object") {
		    		tl.clear().time(0);
		    		tl.remove()
		    	}
		    	$(".imgul .imgli").removeClass("hover");
		    	$(this).parent().addClass("hover");
		    	var $el = $(this).parent().find(".listul");
		    	tl = new TimelineLite();
		    	$(this).parent().find(".showDv").fadeIn();

		    	var $h = $(this).parent().find(".showDv .listul").outerHeight(true);
		    	$(this).parent().find(".showDv .sm_cont").css('min-height', $h);
		    	// $(this).parent().find(".showDv").css('width',"184px");
		    	// $(this).parent().find(".showDv .listul .itemli .active .absContent").hide();

		    	// $(this).parent().find(".showDv .sm_cont").css('height', $h);
		    	console.log($h);
		    	var _num = $el.children().length;
		    	// tl.fromTo($el.children().eq(_num - 1), 0.4, {height: "0%", opacity: 0}, {
		    	// 	height: "100%",
		    	// 	opacity: 1,
		    	// 	ease: Power2.easeIn
		    	// });
		    	for (var i = 0; i < _num ; i++) {
		    		tl.fromTo($el.children().eq(i), 0.2, {y: "-10px", opacity: 0}, {y: "0", opacity: 1, ease: Power2.easeIn})
		    	}
		    	
		    	$('#jiuduyuyan').addClass('sm_cont_free');
		    	$('#freeAbout').addClass('sm_cont_free1');
		    	$('#freeServe').addClass('sm_cont_free2');
		    });
		    $(".showDv .listul .itemli span").mouseenter(function () {

		    	$(".showDv .listul .itemli").removeClass("active disabled");
		    	if ($(this).parent().find(".content").length == 0) {
		    		$(this).parent().addClass("disabled");
		    		return false
		    	}

		    	var $h = $(this).parents(".listul").outerHeight();
		    	$(this).parent().find('.absContent .sm_cont').css('min-height', $h);
		    	// $(this).parent().find('.absContent .sm_cont').css('height', 'auto');
		    	// $(this).parents('.showDv').css('min-height',$h + 60);
		    	var $w = $w_show + $(this).parent().find('.absContent .sm_cont').outerWidth(true);

		    	// console.log(222,$w_show,$(this).parent().find('.absContent .sm_cont').outerWidth(true));

		    	$(this).parents('.showDv').css('width',$w);
		    	$(this).parent().addClass("active");

		    	var $quote = $(this).parent().find(".content p"), mySplitText = new SplitText($quote, {type: "words"}),
		    	splitTextTimeline = new TimelineLite();
		    	mySplitText.split({type: "words"});
		    	$(mySplitText.words).each(function (index, el) {
		    		splitTextTimeline.from($(el), 0.7, {opacity: 0, force3D: true}, index * 0.01);
		    		splitTextTimeline.from($(el), 0.7, {scale: index % 2 == 0 ? 0 : 2, ease: Back.easeOut}, index * 0.01)
		    	})
		    	$(".showDv").css('min-height',$(this).parent().find('.sm_cont').outerHeight() + 60)

		    });
		    $(".showHide").click(function () {
		    	var _flag = $(this).data("flag");
		    	$(".sweet-overlay ,#" + _flag).addClass("active")
		    });
		},
		

	}
</script>
<style scoped lang="less">
@import '../../../static/stylesheet/address.css';
	// @import '../../../static/plugins/swiper2/idangerous.swiper.css';
	@import '../../../static/stylesheet/pages/index.css';

</style>



