<template>
	<div>
		<header-top :nav ="nav"></header-top>
		<div class="mainPart">
			<div class="bannerPart">
				
			</div>
			<div class="newsNavPart">
				<ul class="container newsNavUl">
					<li class="active">
						<a href="javascript:;">全部</a>
					</li>
					
					<li v-for="n in newsNav" :data-cateid="n.id" :key = "n.id" @click="gotoNews($event)">
						<router-link to="/News">
						{{n.name}}
						</router-link>
					</li>
					
				</ul>
			</div>
			<div class="container clearfix contentPart">
				<div class="leftPart">
					<div class="newsContPart">
						<ul class="newsListUl">
							<li v-for = "item in newsList" :key="item.id">
								<div class="imgDv">
									<router-link :to="{name: 'Newsdetails',params: {id: item.id}}">
										<img :src="item.thumb" :alt="item.title">
									</router-link>
								</div>
								<div class="contDv">
									<router-link :to="{name: 'Newsdetails',params: {id: item.id}}">
										<h4 style="width: 496px;">
											{{item.title}}
										</h4>
									</router-link>
									<p class="time"><span></span>{{item.publishedtime}}</p>

									<router-link :to="{name: 'Newsdetails',params: {id: item.id}}" >
										<p class="newsDes" v-html="item.des">
												
										</p>
									</router-link>

									<div class="btnPart">
										<router-link :to="{name: 'Newsdetails',params: {id: item.id}}">查看详情</router-link>
										<ul class="clearfix">
											<li>
												<span></span>{{item.upsnum}}
											</li>
											<li>
												<span></span>{{item.hits}}
											</li>
										</ul>
									</div>
								</div>
							</li>
						</ul>
					</div>
					<div class="pageBox"></div>
				</div>
				<div class="rightPart">
					<hot-activities></hot-activities>
					<hot-news></hot-news>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import headerTop from '@/pages/common/header.vue';
	import hotActivities from '@/pages/common/hotActivities.vue';
	import hotNews from '@/pages/common/hotNews.vue';
	import {mapGetters} from 'vuex'
	export default{
		name: 'Newstags',
		data () {
			return {
				nav: 'news',
				newsNav: [],
				newsSecNav: [],
				newsList: [],
				hotFlag: '',
				request: {
					page: 1,
					limit: 10,
					cateidSecNav: '',
					cateid: ''
				},
				newsTotal: 0 ,//资讯总数,
				countryId: 0
			}
		},
		components: {
			headerTop,
			hotActivities,
			hotNews
		},
		watch: {
		    '$route' (to, from,next) {
		      // 对路由变化作出响应...
		    	var _this = this;
				_this.getCountry();
				_this.hotFlag = localStorage.hottags;
		    }
	    },
		computed: {
			...mapGetters({
				currentCountry: 'currentCountry'
			})
		},
		methods: {
			escapeStringHTML:function(str) {
			    str = str.replace(/&lt;/g,'<');
			    str = str.replace(/&gt;/g,'>');
			    str = str.replace(/&amp;/g,'&');
			    str = str.replace(/&quot;/g,'"');
			    str = str.replace(/&#039;/g,"'");
			    return str;
			},
			timestampToTime (timestamp) {
		        var date = new Date(timestamp * 1000);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
		        var Y = date.getFullYear() + '-';
		        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
		        var D = date.getDate() + ' ';
		        var h = date.getHours() + ':';
		        var m = date.getMinutes() + ':';
		        var s = date.getSeconds();
		        return Y+M+D;
		        // return Y+M+D+h+m+s;
		    },
		    gotoNews (event) {
		    	var _this= this;
				var el =  event.currentTarget;
				localStorage.page = 1;
				if ($(el).data('cateid') != 'newsNavAll') {
					_this.request.cateidSecNav = $(el).data('cateid');
					_this.request.cateid = $(el).data('cateid');
					$('.secNavList li').eq(0).addClass('active').siblings().removeClass('active');

					localStorage.cateidSec = '';
					localStorage.cateid = $(el).data('cateid');

				} else {
					
					_this.request.cateid = '';
					localStorage.cateid = '';
					_this.newsSecNav = '';
					// _this.getNewListData(_this.countryId);
				}

		    },
		    getNewsNavData (id) {
				var _this = this;
				var _id = id;
				$.ajax({
					url: "http://manage.xiaoying.net/article/catesearch?countryid=" + _id,
					type:"GET",
					success: function(res) {
						_this.newsNav = res;
					}
				})
			},
			getHottagsList (id) {
				var _this = this;
				var _id = id;
				$.ajax({
					url: "http://manage.xiaoying.net/article/getarticlebytags?page="+_this.request.page + "&limit=" +_this.request.limit +"&tags=" + _id,
					type:"GET",
					success: function(res) {
						var array = res.data;
						// 'http://xiaoying.net'+item.thumb
						for (var i = 0; i < array.length; i++) {
							if(array[i].thumb.indexOf('http') > -1) {
								array[i].thumb = array[i].thumb;
							}else if(array[i].thumb == '') {
								array[i].thumb = '../../../static/images/news/img.jpg'
							} else {
								array[i].thumb = 'http://manage.xiaoying.net'+ array[i].thumb;
							}
							if (array[i].des) {
								array[i].des = _this.escapeStringHTML(array[i].des);
							}
							var $pre = $('<pre>');
							$pre.html(array[i].des);
							array[i].des = $pre.text();

							array[i].publishedtime = _this.timestampToTime(array[i].publishedtime);
						}
						_this.newsList = array;

						_this.newsTotal= res.count;
						_this.pageCount =  _this.newsTotal%_this.request.limit?(parseInt(_this.newsTotal/_this.request.limit) + 1 ):parseInt(_this.newsTotal/_this.request.limit);

						$('.pageBox').pagination({
							pageCount: _this.pageCount,
							showData: _this.request.limit,
							current:  _this.request.page,
							homePage: '首页',
							endPage: '末页',
							isHide: true,
							count: 2,
							activeCls: 'active',
							coping: true,
							callback: function (api) {
								var getCurrent = api.getCurrent();
								var total = api.getPageCount();
								if (getCurrent>=1 && getCurrent <= total) {
									localStorage.page = api.getCurrent();
									_this.request.page = localStorage.page;
									// console.log(_this.request.page);
									_this.getNewListData(_id);
								}
							}
						});
					}
				})
			},
		    getCountry() {
				var _this = this;
				$.ajax({
					url: "http://manage.xiaoying.net/getcountry",
					type:"GET",
					success: function (res) {
						if(res.status) {
							for (var i = 0; i < res.data.length; i++) {
								if (res.data[i].name == '馒头留学') {
									_this.countryId = res.data[i].id;
									_this.getNewsNavData(_this.countryId);
									_this.getHottagsList(_this.$route.params.id);

								}
							}
						}
					}
				})
			}
		},
		mounted () {
			var _this = this;
			_this.getCountry();
			_this.hotFlag = localStorage.hottags;
		}
	}
</script>
<style scoped lang="less">
@import '../../../static/stylesheet/pages/news.css';

</style>



