import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index'
import Application from '@/pages/application'
// import Application1 from '@/pages/application/index1022'
import Applydetails from '@/pages/application/applydetails'
import Applylist from '@/pages/application/applylist'
import School from '@/pages/school'
import Schoolall from '@/pages/school/indexAll'
import Schooljpco from '@/pages/school/indexJpCollege'
import Schooljpsen from '@/pages/school/indexSenior'
import Schooljpspe from '@/pages/school/indexSpecial'
import Schooldetails from '@/pages/school/schooldetailsjpcoll'
import Case from '@/pages/case'
import Casethanks from '@/pages/case/casethanks'
import Caseadmission from '@/pages/case/caseadmission'
import Casedetails from '@/pages/case/casedetails'
import News from '@/pages/news'
import Newsdetails from '@/pages/news/newsdetails'
import Advantage from '@/pages/advantage'
import Mall from '@/pages/mall'
import Hottags from '@/pages/news/indexTags'


Vue.use(Router)

export default new Router({
	routes: [
	{
		path: '/',
		name: 'Index',
		component: Index
	},
	{
		path: '/application',
		name: 'Application',
		component: Application
		
	},
	// {
	// 	path: '/application1',
	// 	name: 'Application1',
	// 	component: Application1
		
	// },
	{
		path: '/applylist',
		name: 'Applylist',
		component: Applylist
	},
	{
		path: '/applydetails/:id',
		name: 'Applydetails',
		component: Applydetails,

	},
	{
		path: '/school',
		name: 'School',
		component: School
	},
	{
		path: '/school/all/:name',
		name: 'Schoolall',
		component: Schoolall
	},
	{
		path: '/school/jpspecial',
		name: 'Schooljpspe',
		component: Schooljpspe
	},
	{
		path: '/school/jpco',
		name: 'Schooljpco',
		component: Schooljpco
	},
	{
		path: '/school/jpsenior',
		name: 'Schooljpsen',
		component: Schooljpsen
	},
	{
		path: '/schooldetails/:id',
		name: 'Schooldetails',
		component: Schooldetails
	},
	{
		path: '/case',
		name: 'Case',
		component: Case
	},
	{
		path: '/case/casethanks',
		name: 'Casethanks',
		component: Casethanks
	},
	{
		path: '/case/caseadmission',
		name: 'Caseadmission',
		component: Caseadmission
	},
	{
		path: '/casedetails/:id/:r_pic',
		name: 'Casedetails',
		component: Casedetails
	},
	{
		path: '/news',
		name: 'News',
		component: News
	},
	{
		path: '/newsdetails/:id',
		name: 'Newsdetails',
		component: Newsdetails
	},
	{
		path: '/hottags/:id',
		name: 'Hottags',
		component: Hottags
	},
	{
		path: '/advantage',
		name: 'Advantage',
		component: Advantage
	},
	{
		path: '/mall',
		name: 'Mall',
		component: Mall
	}
	],
	scrollBehavior (to, from, savedPosition) {
		return { x: 0, y: 0 }
	}
})



