<template>
    <div>
        <header-top :nav ="nav"></header-top>
    	<div id="indexJpCollege">
    		<div class="bannerPart">
                <img src="../../../static/images/school/schoolBanner.jpg">
            </div>
            <ul class="selectcity container">
                <li class="city_item">
                    <router-link to="/school">语言学校</router-link>
                </li>
                <li class="city_item">
                    <router-link to="/school/jpspecial">专门学校</router-link>
                </li>
                <li class="city_item active">
                    <router-link to="/school/jpco">日本大学</router-link>
                </li>
                <li class="city_item">
                    <router-link to="/school/jpsenior">日本高中</router-link>
                </li>
            </ul>
            <div class="container choosepart">
                <!-- <div class="lineDv clearfix">
                    <span class="s_title">热门排行：</span>
                    <ul class="listul">
                        <li class="item" v-for="n in 5">规模较大的语言学校</li>
                    </ul>
                </div> -->
                <div class="lineDv choose clearfix">
                    <span class="s_title">学校名称：</span>
                    <div class="inputDv">
                        <input type="text" placeholder="请输入学校名称" v-model="request.name" @keyup.13="change('schoolname',request.name)">
                        <i class="search" @click="change('schoolname',request.name)"><img src="../../../static/images/school/search_icon.png"></i>
                    </div>
                </div>
                <div class="lineDv clearfix">
                    <span class="s_title">选择地区：</span>
                    <select name="" id="area" class="select2" width="290">
                        <option value="">全部</option>
                        <option :value="n" v-for="n in area" :key="n">{{n}}</option>
                    </select>
                    <!-- <span class="s_title">选择城市：</span>
                    <select name="" id="city" class="select2" width="290">
                        <option value="">全部</option>
                        <option :value="n.id" v-for="n in city">{{n.name}}</option>
                    </select> -->
                </div>
               <!--  <div class="lineDv choose clearfix">
                    <span class="s_title">学校性质：</span>
                    <ul class="listul">
                        <li class="item active" @click="change('school_type','')">不限</li>
                        <li class="item" @click="change('school_type','1')">语言学校</li>
                        <li class="item" @click="change('school_type','2')">高中/专门学校</li>
                        <li class="item" @click="change('school_type','3')">本科</li>
                        <li class="item" @click="change('school_type','4')">大学别科/预科</li>
                        <li class="item" @click="change('school_type','5')">大学院修士</li>
                    </ul>
                </div> -->
                <div class="lineDv choose clearfix">
                    <span class="s_title">学校性质：</span>
                    <ul class="listul">
                        <li class="item active" @click="change('attribute','')">不限</li>
                        <li class="item" @click="change('attribute','私立')">私立</li>
                        <li class="item" @click="change('attribute','公立')">公立</li>
                        <li class="item" @click="change('attribute','国立')">国立</li>
                    </ul>
                </div>
                <!-- <div class="lineDv choose clearfix">
                    <span class="s_title">国内排名：</span>
                    <ul class="listul">
                        <li class="item active" @click="change('rank','')">不限</li>
                        <li class="item" @click="change('rank','1')">1~10名</li>
                        <li class="item" @click="change('rank','2')">11~20名</li>
                        <li class="item" @click="change('rank','3')">21~50名</li>
                    </ul>
                </div> -->
            </div>
            <div class="resultPart container">
                <h3 class="search_title">共找到<font>{{count}}</font>所学校</h3>
                <ul class="schoolList">
                    <li class="item_school clearfix" v-for="n in lists">
                        <router-link class="imgDv left" :to="{ name: 'Schooldetails', params: { id: n.id }}">
                            <img :src="'http://manage.xiaoying.net/Uploads/resources/school/'+n.defaultimage" alt="">
                        </router-link>
                        <div class="info left">
                            <router-link :to="{ name: 'Schooldetails', params: { id: n.id }}">
                                <h3 class="name"><span>{{n.name}}</span><span>{{n.englishname}}</span></h3>
                                <p class="weburl">{{n.weburl}}</p>
                                <p class="description" v-html="n.description"></p>
                                <!-- <ul class="slistDv">
                                    <li class="slineDv">学校性质：{{n.xingzhi_name}}</li>
                                    <li class="slineDv">国内排名：{{n.rank_local}}</li>
                                    <li class="slineDv">每年学费：{{n.cost_fee | filterfee}}万(日元)</li>
                                    <li class="slineDv">电话：{{n.contact}}</li>
                                    <li class="slineDv">录取比例：{{n.competition}}%</li>
                                    <li class="slineDv full">学校地址：{{n.address}}</li>
                                </ul> -->
                            </router-link>
                        </div>
                        <div class="btn-wapper left">
                            <router-link :to="{ name: 'Schooldetails', params: { id: n.id }}" class="admissions">领取官方招生手册</router-link>
                            <router-link  :to="{ name: 'Schooldetails', params: { id: n.id }}" class="subscribe">招生官面试预约</router-link>
                            <router-link to="/Application"  class="apply">官方免费申请</router-link>
                        </div>
                    </li>
                </ul>
                <div id="jqPaginator" class="pageNav" style="display: block;"></div>
            </div>
    	</div>
    </div>
</template>
<script>
    import headerTop from '@/pages/common/header.vue';
	export default{
		name: 'indexJpCollege',
		data() {
			return {
                nav: 'school',
	            area: [],
	            city: [],
	            request:{
	                page:1,
	                limit:10,
                    category: '3',
                    name: '',
                    area: '',
                    attribute: ''
	                // school_type:"",
	                // xingzhi_name:"",
	                // rank:"",
	                // nowcid:"",
	                // schoolname:""
	            },
	            count:"",
	            lists:[],
	            tuition: [{
	                name: "60万円以下",
	                id: "1"
	            }, {
	                name: "60-70万円",
	                id: "2"
	            }, {
	                name: "70-80万円",
	                id: "3"
	            }, {
	                name: "80万円以上",
	                id: "4"
	            }]
			}

        },
        components: {
            headerTop
        },
        filters:{
            filterfee: function(val){
                if (val) {
                    val = parseFloat(val);
                    val = val.toFixed(2);
                }
                return val;
            }
        },
        methods: {
            getarea: function(_id) {
                var _data = {};
                if (_id) {
                    _data = {
                        id : _id
                    }
                }
                var _this = this;
                $.ajax({
                    url:"http://manage.xiaoying.net/getarea",
                    type:"post",
                    data:_data,
                    success:function(res){
                        if (res.status) {
                            if (!_id) {
                                _this.area = res.data;
                            }else{
                                _this.city = res.data;
                            }
                            _this.$nextTick(function(){
                                $("#city").select2({
                                    width: "300px",
                                })
                            })
                        }
                    }
                })
            },
            escapeStringHTML:function(str) {
                str = str.replace(/&lt;/g,'<');
                str = str.replace(/&gt;/g,'>');
                str = str.replace(/&amp;/g,'&');
                str = str.replace(/&quot;/g,'"');
                str = str.replace(/&#039;/g,"'");
                return str;
            },
            change: function(key,val){
                this.request[key] = val;
                this.request.page = 1;
                this.getdata();
            },
            getdata: function(){
                var _this = this;
                $.ajax({
                    url:"http://manage.xiaoying.net/yuanxiao/api/index",
                    type:"get",
                    data: _this.request,
                    success:function(res){
                        if (res.code == 0) {
                            var arr = res.data;
                             for (var i = 0; i < arr.length; i++) {

                                arr[i].description = _this.escapeStringHTML(arr[i].description)
                            }
                            _this.lists =arr;
                            _this.count = res.count
                            _this.area = res.more.allregion;

                           
                            $("#jqPaginator").html("");
                            if (res.count > 0) {
                                $('#jqPaginator').jqPaginator({
                                    totalCounts: parseInt(res.count),
                                    pageSize: _this.request.limit,
                                    visiblePages: 7,
                                    currentPage: _this.request.page,
                                    first: "<a>首页</a>",
                                    last: "<a>末页</a>",
                                    prev: "<a>上一页</a>",
                                    page: "<a class='page'>{{page}}</a>",
                                    next: "<a>下一页</a>",
                                    onPageChange: function(num, type) {
                                        if (type == "change") {
                                //         	$("body, html").animate({
				                            //     scrollTop: $(".choosepart").offset().top - 130
				                            // }, 200)
                                            _this.request.page = num;
                                            _this.getdata();
                                            $("body, html").animate({
                                                scrollTop: $(".choosepart").offset().top - 150
                                            }, 200)
                                        }
                                    }
                                })
                            }
                        }
                        // console.log("xxx",res);
                    }
                })
            }
        },
        mounted: function() {
            var _this = this;
            // this.getarea();
            this.getdata();
            $(".choose .item ").click(function(){
                $(this).addClass("active").siblings().removeClass("active");
            })
            $("#area,#city").select2({
                width: "300px",
            })

            $("#area").on("select2:select",function(e){
                _this.change('area',$("#area").val());
                // _this.getarea($("#area").val());
            });
            // $("#city").on("select2:select",function(e){
            //     _this.change('nowcid',$("#city").val());
            // });
        }
	}
</script>
<style scoped lang="less">
@import '../../../static/stylesheet/pages/schoolPublic.css';
.resultPart {
    .schoolList {
        .item_school {
            .imgDv {
                width: 260px;
            }
            .info {
                width: 640px;
                .name {
                    line-height: 40px;
                    margin-bottom: 0;
                }
            }
        }
    }
}
</style>



