<template>
	<div id="application">
		<header-top :nav ="nav"></header-top>
		<div class="mainPart applicationForm">
			<div class="bannerPart">
				
			</div>
			<div class="processPart">
				<h4 class="title">馒头在线申请流程 成功就在这一步</h4>
				<h5 class="secTitle">mantou free application process  make you success in this step</h5>
				<div class="proTableDv container">
					<div v-for="n in processData">
						<div class="imgDv">
							<img :src="n.imgUrl">
						</div>
						<h5 v-html="n.title"></h5>
						<p v-html="n.des"></p>
					</div>
				</div>
			</div>
			<div class="formPart">
				<div class="container">
					<div class="titleDv">
						<h4>测一测你能否考入日本名校</h4>
					</div>
					<p class="notice">请完善你的个人信息及教育背景，这些信息我们将严格保密！</p>


					<div class="formDv">
					<!-- 		<h4 class="title">学习信息</h4>
						<div class="chooseListPart">
							<div class="clearfix">
								<div>日语能力</div>
								<div>
									<ul class="clearfix">
										<li>N1</li>
										<li>N1</li>
									</ul>
								</div>
							</div>
							<div class="clearfix">
								<div>出国时间</div>
								<div>
									<select id="grade">
										<option>大一</option>
										<option>大二</option>
									</select>
								</div>
							</div>
						</div>
						<h4 class="title">基础信息</h4>
						<div class="chooseListPart">
							<div class="clearfix">
								<div>姓名</div>
								<div>
									<input type="text" name="" placeholder="请输入那您的姓名">
								</div>
							</div>
							<div class="clearfix">
								<div>手机</div>
								<div>
									<input type="text" name="" class="phone" placeholder="请输入你的手机号">
									<button class="getCodeBtn">获取验证码</button>
								</div>
							</div>
							<div class="clearfix">
								<div>验证码</div>
								<div>
									<input type="text" name="" placeholder="请输入那您的验证码">
								</div>
							</div>
						</div>
						<button class="submitBtn">提交</button> 
					-->
						<div class="fillInfo clearfix xy_form xy_form_application">
							<h4 class="title">学习信息</h4>
							<div class="CRM_TEMPLET japanLevel" data-templet="CRM_TEMPLET_XY_b12" style="display: none;">
				                <div class="item select clearfix jpLevel">
				                	<span class="ss_title CRM_TEMPLET_NAME"></span>
				                    <select class=""></select>
				                </div>
				            </div>
				            <div class="CRM_TEMPLET" data-templet="CRM_TEMPLET_XY_b09" style="display: none;">
				                <div class="item select clearfix abroadTime">
				                	<span class="ss_title CRM_TEMPLET_NAME"></span>

				                    <select class=""></select>
				                </div>
				            </div>
				             <div class="CRM_TEMPLET" data-templet="CRM_TEMPLET_XY_b19" style="display: none;">
				                <div class="item select clearfix grand">
				                	<span class="ss_title CRM_TEMPLET_NAME"></span>

				                    <select class=""></select>
				                </div>
				            </div>
				            <div class="CRM_TEMPLET" data-templet="CRM_TEMPLET_SELECT" style="display: none;">
				                <div class="item select clearfix">
				                	<span class="ss_title CRM_TEMPLET_NAME"></span>

				                    <select class=""></select>
				                </div>
				            </div>
				            <div class="CRM_TEMPLET" data-templet="CRM_TEMPLET_XY_b13" style="display: none;">
				                <div class="item select clearfix full city">
				                	<span class="ss_title CRM_TEMPLET_NAME"></span>

				                    <select class=""></select>
				                </div>
				            </div>
				            <div class="CRM_TEMPLET" id="name" data-templet="CRM_TEMPLET_XY_a01" style="display: none;">
				                <div class="item name">
				                	<span class="ss_title CRM_TEMPLET_NAME"></span>
				                    <input type="text" class="" placeholder="">
				                </div>
				            </div>
				            <div class="CRM_TEMPLET" data-templet="CRM_TEMPLET_INPUT" style="display: none;">
				                <div class="item">
				                	<span class="ss_title CRM_TEMPLET_NAME"></span>
				                    <input type="text" class="" placeholder="">
				                </div>
				            </div>
				            <div class="CRM_TEMPLET" data-templet="CRM_TEMPLET_XY_a02" style="display: none;">
				                <div class="item phone">
				                	<span class="ss_title CRM_TEMPLET_NAME"></span>

				                    <input type="text" class="" placeholder="">
				                    <button class="getCodeBtn ">获取验证码</button>
				                </div>
				            </div>
				        </div>
				        <div class="btn">
				            <input type="button" class="xy_submit xy_submit_application" value="名额有限 马上申请">
				        </div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<!-- <script src="http://crm.xiaoying.net/Public/js/xiaoying_form.js"></script> -->

<script>
	import headerTop from '@/pages/common/header.vue';

	export default{
		name: 'Application',
		data () {
			return {
				nav: 'application',

				processData: [
					{
						imgUrl: '../../../static/images/application/form/p_icon1.png',
						title: "1.  填写信息",
						des: "信息填写完成后可免费咨询<br>资深名师为您拟定留学规划"
					},
					{
						imgUrl: '../../../static/images/application/form/p_icon2.png',
						title: "2.  选定学校",
						des: "结合学生的实际情况<br>提供详尽的择校建议和留学规划"
					},
					{
						imgUrl: '../../../static/images/application/form/p_icon3.png',
						title: "3.  材料准备",
						des: "全程专属文案老师知道申请<br>所需的各项材料，申请信息即时查询"
					},
					{
						imgUrl: '../../../static/images/application/form/p_icon4.png',
						title: "4.  成功留学",
						des: "顺利获得offer，开启留学之旅<br>签证办理和出行准备随时找我"
					}
				],
				jpLevelData: []

			}
		},
		computed: {
			
		},
		components: {
			headerTop
		},
		methods: {

			initForm: function () {
				var _this = this;
					
			  	$(".xy_form_application").xiaoyingForm({
			        parameter: [
			            {errorMes: "请选择日语水平", id: "XY_b12", rename: "日语水平", reg: "required"},
			            {errorMes: "请选择留学时间", id: "XY_b09", reg: "required"},
			            {errorMes: "请填写所在年级", id: "XY_b19", reg: "required"},
			            {errorMes: "请选择所在城市", id: "XY_b13", reg: "required"},
			            {errorMes: "姓名格式不正确", id: "XY_a01", rename: "姓名"},
			            {errorMes: "电话格式不正确", id: "XY_a02", rename: "手机"},
			            {errorMes: "验证码格式不正确",id: "XY_c02",reg: "required"}
			            // {errorMes: "微信格式不正确", id: "XY_b11", reg: "required"},
			        ],
					sendMes: ".getCodeBtn",
			        submitBtn: ".xy_submit_application",
			        beforeSendData: function (_data, cb) {
			            cb(_data);
			            // console.log(111,_data)
			        },
			        afterLoadField: function () {
			            $(".city select,.grand select").select2({
			                width: "400px",
			            })
			            $(".city,.grand ").css({'margin-bottom': '30px'})
			           
			            $('.fillInfo').prepend($('<h4>').text('学习信息').css({
			            	"font-size": "20px",
							"font-weight": "700",
							"color": "#333333",
							// "line-height": "50px",
							"padding-top": "35px",
							"padding-bottom": "14px",
							"border-bottom": "1px solid #e4e0e0",
			            }));
			            $('.city').append($('<h4>').text('基本信息').css({
			            	"font-size": "20px",
							"font-weight": "700",
							"color": "#333333",
							// "line-height": "50px",
							"border-bottom": "1px solid #e4e0e0",
							"padding-top": "35px",
							"padding-bottom": "14px",
			            }))
			            $('.jpLevel').css({'padding-top': "30px"})
			            //日语水平
			            var $ul = $('<ul>');
			            for (var i = 1; i < $('.jpLevel select option').length; i++) {
			            	$('.jpLevel select option').eq(i).text();
			            	// console.log($('.jpLevel select option').eq(i).text())
			            	var $li = $('<li>').text($('.jpLevel select option').eq(i).text());
			            	$ul.append($li);
			            }
			            $('.jpLevel').append($ul);
			            $('.jpLevel span').css({'float': "left"});
			            $('.jpLevel select').css({'width': "0"});
			            $('.jpLevel ul').css({
			            	'float': "left",
			            	"width": "776px"
			            })
			            $('.jpLevel ul li').css({
			            	"display": "inline-block",
			            	"width": "110px",
							"line-height": "38px",
							"color": "#36c2a1",
							"margin-right": "16px",
							"margin-bottom": "30px",
							"text-align": "center",
							"border": "1px solid #36c2a1",
							"border-radius": "55px",
							"cursor": "pointer"
			            })
			           
			            
			            $('.jpLevel ul li').hover(function () {
			            	$(this).css({
				            	"color": "#fff",
								"background-color": "#36c2a1",
				            });
			            },function () {
			            	if (!$(this).hasClass('active')) {
			            		$(this).css({
					            	"color": "#36c2a1",
									"background-color": "transparent",
					            });
			            	}
			            })
			            $('.jpLevel ul li').click(function () {
			            	$(this).addClass('active').siblings().removeClass('active');
			            	$(this).css({
				            	"color": "#fff",
								"background-color": "#36c2a1",
				            }).siblings().css({
				            	"color": "#36c2a1",
								"background-color": "transparent",
				            });
			            	$(this).parents('.jpLevel').find('select').val($(this).text());
			            })
			            //出国时间
			            var $ul1 = $('<ul>');
			            for (var i = 1; i < $('.abroadTime select option').length; i++) {
			            	$('.abroadTime select option').eq(i).text();
			            	// console.log($('.abroadTime select option').eq(i).text())
			            	var $li = $('<li>').text($('.abroadTime select option').eq(i).text());
			            	$ul1.append($li);
			            }
			            $('.abroadTime').append($ul1);
			            $('.abroadTime span').css({'float': "left"});
			            $('.abroadTime select').css({'width': "0"});
			            $('.abroadTime ul').css({
			            	'float': "left",
			            	"width": "776px"
			            })
			            $('.abroadTime ul li').css({
			            	"display": "inline-block",
			            	"width": "110px",
							"line-height": "38px",
							"color": "#36c2a1",
							"margin-right": "16px",
							"margin-bottom": "30px",
							"text-align": "center",
							"border": "1px solid #36c2a1",
							"border-radius": "55px",
							"cursor": "pointer"
			            })
			   			$('.abroadTime ul li').hover(function () {
			            	$(this).css({
				            	"color": "#fff",
								"background-color": "#36c2a1",
				            });
			            },function () {
			            	if (!$(this).hasClass('active')) {
			            		$(this).css({
					            	"color": "#36c2a1",
									"background-color": "transparent",
					            });
			            	}
			            })
			            $('.abroadTime ul li').click(function () {
			            	$(this).addClass('active').siblings().removeClass('active');
			            	$(this).css({
				            	"color": "#fff",
								"background-color": "#36c2a1",
				            }).siblings().css({
				            	"color": "#36c2a1",
								"background-color": "transparent",
				            });
			            	$(this).parents('.abroadTime').find('select').val($(this).text());
			            })
			      
			        },
			        validatorError: function (mes, node) {
			            alert(mes);
			        },
			        submitSuccess: function () {
			            alert("申请成功！请等到老师与您联系");
			            $(".btn input").val("提交成功");
			            $("#subb span").text("提交成功");
			        },
			        submitError: function () {
			        }
			    });
				
			}
		},
		mounted () {
			var _this = this;
			$.getScript("http://crm.xiaoying.net/Public/js/xiaoying_form.js",function(){
				_this.initForm();
			});
			$("#grade").select2({
                width: "300px",
            })
            // $("#grade").on("select2:select",function(e){
            //     console.log(111,$("#grade").val());
              
            // });
           
           


            //placeholder  兼容IE9
			var hasPlaceholderSupport = function() {
			var attr = "placeholder";
			var input = document.createElement("input");
			return attr in input;
			}
			var support = hasPlaceholderSupport();
			if (!support) {
			$('[placeholder]').focus(function() {
			  var input = $(this);
			  if (input.val() == input.attr('placeholder')) {
			    input.val('');
			    input.removeClass('placeholder');
			  }
			}).blur(function() {
			  var input = $(this);
			  if (input.val() == '' || input.val() == input.attr('placeholder')) {
			    input.addClass('placeholder');
			    input.val(input.attr('placeholder'));
			  }
			}).blur().parents('form').submit(function() {
			  $(this).find('[placeholder]').each(function() {
			    var input = $(this);
			    if (input.val() == input.attr('placeholder')) {
			      input.val('');
			    }
			  })
			});
			}
		}

	}
</script>
<style scoped lang="less">
@import '../../../static/stylesheet/pages/application.css';

</style>



