<template>
	<div>
		<div class="parentsCare">
			<h4>热门资讯</h4>
			<div class="news-right-hot" v-for="n in hotNewsList">
				<router-link :to="{name: 'Newsdetails',params: {id: n.id}}">
					<div class="imgDv">
						<img :src="n.thumb" :alt="n.title">
					</div>
					<div class="first-text">
						<span class="title">{{n.title}}</span>
						<p class="des" v-html="n.des"></p>
						<!-- <span>{{n.des}}</span> -->
						<p>
							<em></em>
							<font>{{n.hits}}</font>
							<em></em>
							<font>{{n.publishedtime}}</font>
						</p>
					</div>
				</router-link>
			</div>
		</div>	
	</div>
</template>
<script>
	export default{
		name: 'parentsCare',
		data () {
			return {
				hotNewsList: []
			}
		},
		watch: {
			'$route' (to, from,next) {
		      // 对路由变化作出响应...
		      this.getCountry();
		  }
		},
		methods: {
			escapeStringHTML:function(str) {
				str = str.replace(/&lt;/g,'<');
				str = str.replace(/&gt;/g,'>');
				str = str.replace(/&amp;/g,'&');
				str = str.replace(/&quot;/g,'"');
				str = str.replace(/&#039;/g,"'");
				return str;
			},
			timestampToTime (timestamp) {
		        var date = new Date(timestamp * 1000);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
		        var Y = date.getFullYear() + '-';
		        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
		        var D = date.getDate() + ' ';
		        var h = date.getHours() + ':';
		        var m = date.getMinutes() + ':';
		        var s = date.getSeconds();
		        return Y+M+D;
		        // return Y+M+D+h+m+s;
		    },
		    getData (_id) {
		    	var _this = this;
		    	$.ajax({
		    		url:"http://manage.xiaoying.net/article/contentssearch?countryid=" + _id,
		    		type:"get",
		    		success: function(res) {
		    			if (res) {
		    				var array = res.data;
		    				for (var i = 0; i < array.length; i++) {
		    					if(array[i].thumb.indexOf('http') > -1) {
		    						array[i].thumb = array[i].thumb;
		    					}else if(array[i].thumb == '') {
		    						array[i].thumb = '../../../static/images/news/img.jpg'
		    					} else {
		    						array[i].thumb = 'http://manage.xiaoying.net'+ array[i].thumb;
		    					}

		    					array[i].des = _this.escapeStringHTML(array[i].des);

		    					array[i].publishedtime = _this.timestampToTime(array[i].publishedtime);
		    				}
		    				_this.hotNewsList = array.splice(0,5);
		    			}
		    		}
		    	})
		    },
		    getData1 (times) {
		    	var _this = this;
		    	$.ajax({
		    		url:"http://manage.xiaoying.net/article/hotlist?times=5",
		    		type:"get",
		    		success: function(res) {
		    			if (res) {
		    				var array = res;
		    				for (var i = 0; i < array.length; i++) {

		    					
		    					
		    					if(array[i].thumb.indexOf('http') > -1) {
									array[i].thumb = array[i].thumb;
								}else if(array[i].thumb == '') {
									array[i].thumb = '../../../static/images/news/img.jpg'
								} else if (array[i].thumb.indexOf('ueditor/php/upload/image') > -1) {
									array[i].thumb = 'http://manage.xiaoying.net'+ array[i].thumb;
								}else if(array[i].thumb.indexOf('files/defaul') > -1){
									array[i].thumb = 'http://www.xiao-ying.net/'+array[i].thumb;
								}else if(array[i].thumb.indexOf('Public/blog') > -1){
									var _src = array[i].thumb.replace('Public/blog','files/blog');
									array[i].thumb = 'http://www.xiao-ying.net/'+_src;
								}else {
									array[i].thumb = 'http://manage.xiaoying.net'+ array[i].thumb;
								}
		    					array[i].des = _this.escapeStringHTML(array[i].des);

		    					array[i].publishedtime = _this.timestampToTime(array[i].publishedtime);
		    				}
		    				_this.hotNewsList = array;
		    			}
		    		}
		    	})
		    },
		    getCountry() {
		    	var _this = this;
		    	$.ajax({
		    		url: "http://manage.xiaoying.net/getcountry",
		    		type:"GET",
		    		success: function (res) {
		    			if(res.status) {
		    				for (var i = 0; i < res.data.length; i++) {
		    					if (res.data[i].name == '馒头留学') {
		    						_this.countryId = res.data[i].id;
		    						_this.getData(_this.countryId)
		    					}
		    				}
		    			}
		    		}
		    	})
		    }
		},
		mounted () {
			this.getData1();
			// this.getCountry();
		}
	}
</script>
<style scoped lang="less">
@import url("../../../static/stylesheet/function.less");
@import url("../../../static/stylesheet/publicConfig.less");
.news-right-promote {
	width: 100%;
	margin-top: 15px
}

.news-right-promote ul {
	padding-top: 8px
}

.news-right-promote li {
	position: relative;
	line-height: 28px;
	color: #333;
	background: url(/proc/News/Public/images/icon-list-arrow_93ff1f07.png) no-repeat;
	background-position: 0 14px;
	padding-left: 10px;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis
}

.news-right-promote li a {
	color: #666;
	font-size: 14px
}

.news-right-promote li a:hover {
	color: #01a4e5;
	text-decoration: underline
}

.news-right-hot {
	width: 100%;
	// margin-top: 15px;
	// overflow: hidden;
	padding-bottom: 20px;
	&>a {
		display: table;
		width: 300px;
		font-size: 0;
		&>div {
			display: table-cell;
			vertical-align: middle;
		}
		.imgDv {
			width: 125px;
			img {
				display: block;
				width: 125px;
				height: 84px;

			}
		}
		.first-text {
			width: 170px;
			font-size: 14px;
			padding-left: 10px;
			.title {
				font-weight: 700;
				line-height: 20px;
				.multi-overflow(1);
			}
			.des {
				height: 36px;
				font-size: 12px;
				.multi-overflow(2);
			}
		}
	}
}

.news-right-hot .first-text span {
	display: inline-block;
	line-height: 18px;
	color: #666;
	overflow: hidden
}
.news-right-hot .first-text .title {
	font-weight: 700;
	// .single-overflow();

}

.news-right-hot .first-text p {
	font-size: 13px;
	color: #999;
	margin-top: 4px
}
.news-right-hot .first-text .des {
	height: 36px;
	.multi-overflow(2);
	// margin-top: 2px
}
.news-right-hot .first-text p em {
	display: inline-block;
	height: 14px;
	width: 14px;
	font-style: normal;
	background: url(data:image/png;base64,) no-repeat;
	background-size: 100% 100%;
	position: relative;
	top: 2px;
	margin-left: 15px
}

.news-right-hot .first-text p em:first-child {
	width: 25px;
	height: 15px;
	background: url(data:image/png;base64,) no-repeat;
	background-size: 100% 100%;
	margin-left: 0
}
.parentsCare {
	background-color: #fff;
	.border-radius(4px);
	padding: 6px 20px 40px;
	margin-bottom: 20px;
	h4 {
		font-size: 16px;
		color: #262626;
		line-height: 44px;
		border-bottom: 1px solid @themeColor;
		margin-bottom: 20px;
	}
	.careQuestionList {
		padding-top: 7px;
		li {
			float: left;
			width: 50%;
			text-align: center;
			font-size: 14px;
			a {
				display: block;
				.iconDv {
					line-height: 68px;
					img {
						vertical-align: middle;
						margin-top: -2px;
					}
				}
				p {
					font-size: 14px;
					line-height: 30px;
					color: #3a3a3a;
					padding: 0 10px;
					.single-overflow();
				}
			}
		}
	}
}
</style>



